a:link { text-decoration: none; }
a:visited { text-decoration: none; }
a:hover { text-decoration: none; }
a:active { text-decoration: none; }

.navbar {
  background-color: #ffffff;
  display: flex;
  justify-content: center;
  padding: 0% 0% 0% 0%;
  margin: 0% 0% 0% 0%;
  border-radius: 0px;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
}

.navbarContainer {
  display: flex;
  width: 100%;
  max-width: 1920px;
  
  align-items: flex-end;
  margin: 0px 0px 0px 0px;
}

.navbarLeft {
  width: 100%;
  display: flex;
  flex-direction: row;
}



.navbarRight {
  display: flex;
  align-items: right;
  justify-content: right;
  float: right;
  margin: 0% 0% .5% 0%;
}

.navbarLogoImage {
  width: 20%;
  margin: 10px 0px 0px 10px;
}





.navbarIcon {
  width: 4%;
  padding: 0% 2% 0% 0%;
  cursor: pointer;
  align-items: right;
  justify-content: right;
  float: right;
}

.navbarLogoImage:hover,
.navbarButton:hover,
.navbarWalletButton:hover,
.navbarIcon:hover {
  -webkit-transform: scale(1.10);
  -moz-transform: scale(1.10);
  -o-transform: scale(1.10);
  transform: scale(1.10);
}

.navbarLogoImage:active,
.navbarButton:active,
.navbarWalletButton:active,
.navbarIcon:active {
  -webkit-transform: scale(1.15);
  -moz-transform: scale(1.15);
  -o-transform: scale(1.15);
  transform: scale(1.15);
}


.navbarMobileButton {
  display: none;
}
.MobileDN {
  display: none;
}
.navbarMobile {
  display: none;
}

.twitterIcon {
  padding: 0% 1% 0% 0%;
  margin: -1% 0% 0% 0%;
}

.navbarMobileContainer {
  position: absolute;
  top: 0;
  left: 0;
  height: 150px;
  width: 100%;
  z-index: -1;
  overflow: hidden;
  transition: z-index 1s linear;
}

.navbarMobileContainerActive {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 999;
  overflow: hidden;
}

@media only screen and (max-width: 1100px) {
  .navbarBoxImage {
    display: none;
  }
  .navbarBox {
    margin-top: 30px;
  }
  .navbarBoxTitle {
    margin: 0px 0px 0px 0px;
    font-size: 48px;
  }
  .navbar {
    padding: 0 20px;
    margin: 20px 20px;
  }

  .navbarIcon {
    width: 8%;
    
  }

  .navbarSocialsContainer {
    margin: 4% -4% 0% 0%;
  }

  .navbarLogoImage {
    width: 100%;
    padding: 0% 0% 0% 0%;
    margin: 0% 0% 0% 0%;
  }

  .twitterIcon {
    transform: scale(2.1);
    margin: 1.25% 3.8% 0% 0%;
    padding: 0% 0% 0% 0%;
  }
}

@media only screen and (max-width: 870px) {
  .navbar {
    margin: 0px 0px 0px 0px;
  }
  /*.navbarCenterBottom {
    display: none;
  }*/
  .navbarBoxTitle {
    font-size: 28px;
  }

  .navbarIcon {
    width: 8%;
    
  }

  .navbarSocialsContainer {
    margin: 4% -4% 0% 0%;
  }

  .navbarLogoImage {
    width: 100%;
    padding: 0% 0% 0% 0%;
    margin: 0% 0% 0% 0%;
  }

  .twitterIcon {
    transform: scale(2.1);
    margin: 1.25% 3.8% 0% 0%;
    padding: 0% 0% 0% 0%;
  }
}
